.heading{
  &__01{
    font-family: 'Ropa Sans', sans-serif;
    text-align: center;
    margin: 50px 0 30px 0;
    font-size: $font-size-base * 2.5;
    background: url("../img/heading01bg.svg") center bottom no-repeat;
    background-size: 250px;
    padding: 0 0 25px 0;
    @include media-breakpoint-up(md) {
      margin: 100px 0 60px 0;
      padding: 0 0 40px 0;
      font-size: $font-size-base * 4.5;
      background-size: 390px;
    }
    &.bgwh{
      background-image: url("../img/heading01bg__bgwihte.svg") ;
      color: $dojocon-green;
    }
  }
  &__02{
    font-family: 'Ropa Sans', sans-serif;
    text-align: center;
    margin: 40px 0 20px 0;
    @include media-breakpoint-up(md) {
      margin: 80px 0 50px 0;
      font-size: $font-size-base * 2;
    }
    span{
      color: #fff;
      display: inline-block;
      border-radius: 30px;
      background-color: $dojocon-blue;
      padding: 5px 30px;
    }
  }
}
