.main-visual{
  height: 450px;
  position: relative;
  background: url("../img/main-visual__sp.png") no-repeat center top;
  background-size: 100%;
  @include media-breakpoint-up(md) {
    height: 1280px;
    background: url("../img/main-visual.png") no-repeat center top;
    background-size: auto;
  }
  &__title{
    font-family: 'Ropa Sans', sans-serif;
    position: absolute;
    z-index: 1;
    width: 100%;
    text-align: center;
    color: #1f4d5e;
    top: 20px;
    @include media-breakpoint-up(md) {
      font-size: $font-size-base*7;
      top: 40px;
    }
  }
  &__description{
    position: absolute;
    z-index: 1;
    width: 100%;
    color: #1f4d5e;
    top: 120px;
    text-align: center;
    @include media-breakpoint-up(md) {
      font-size: $font-size-base*2;
      top: 180px;
    }
  }
}
